// Helper classes

@mixin absolute-content {
    position: absolute;
    content: "";
}

// creates pseudo elements
.beforeafter {
    position: relative;
    &:before,
    &:after {
        @include absolute-content;
    }
}

.before {
    position: relative;
    &:before {
        @include absolute-content;
    }
}

.after {
    position: relative;
    &:after {
        @include absolute-content;
    }
}

.screen-reader-text {
    display: none;
}

.reset-input {
    border: none;
    &:active,
    &:focus {
        outline: none;
    }
}

.reset-btn {
    background: transparent;
    border: none;
    &:active,
    &:focus {
        outline: none;
    }
}

a {
    &.rst {
        text-transform: initial;
        color: initial;
        text-decoration: none;
        &.hover-underline:hover {
            text-decoration: underline;
        }
    }
    &.informal {
        text-transform: initial;
        &.underline{
            text-decoration: underline;
        }
    }
}

a .svg-icon {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
}

.jump-scroll {
    &:hover {
        text-decoration: none !important;
    }
    .svg-icon {
        transition: 0.25s;
        &:hover {
            transform: translate(0, 5px);
        }
    }
}

hr {
    border-bottom: 1px solid;
    &.short {
        width: 94px;
    }
    &.shorter {
        width: 58px;
    }
    &.wide{
        width: 100%;
    }
    &.blue {
        border-color: $lightblue;
    }
    &.white {
        border-color: $wht;
    }
    &.black {
        border-color: $blk;
    }
    &.left {
        margin-left: 0;
        // IE11
        text-align: left;
    }
}

.announcement-title-banner {
    font-weight: 100;
    width: 100%;
    padding: 00px 0;
    margin: 0;
    text-align: center;
    h2 {
        font-size: 1.75em;
    }
}

// Inbox classing. Build now categorize later
.feature-case {
    position: relative;
    &:first-of-type {
        background: #f8f5f5;
        &:before {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            height: 50%;
            margin: auto 0;
            border-right: 1px solid $lightblue;
        }
    }
    &:last-of-type {
        background: #f4f7f9;
    }
}

.block {
    display: block !important;
}

.inline-block {
    display: inline-block !important;
}

.flex {
    display: flex !important;
}

.grid {
    display: grid !important;
}

.relative {
    position: relative !important;
}

.absolute {
    position: absolute !important;
}

.fixed {
    position: fixed !important;
}
